
.image {
  filter: drop-shadow(15px 10px 9px #4e4e4e);
  width: 200px;
  height: 200px;
  object-fit: contain;
}

.product-detail {
  border: 1px solid black;
  height: 25px;
  padding-left: 0.1rem;
  align-items: center;
}

.price {
  border: 1px solid black;
  height: 25px;
  padding-left: 0.1rem;
  align-items: center;
}

@media only screen and (min-width: 640px) {
  .product-detail {
    border: 1px solid black;
    height: 25px;
    align-items: center;
    font-size: 0.7rem;
  }

  .image {
    filter: drop-shadow(15px 10px 9px #4e4e4e);
    width: calc(260px * 0.6);
    height: calc(210px * 0.6);
  }
}
@media only screen and (min-width: 1024px) {
  .image {
    filter: drop-shadow(15px 10px 9px #4e4e4e);
    width: calc(260px * 0.4);
    height: calc(210px * 0.5);
  }

  .product-detail {
    border: 1px solid black;
    height: 25px;
    align-items: center;
    font-size: 0.7rem;
  }
}

@media only screen and (min-width: 1280px) {
  .image {
    filter: drop-shadow(15px 10px 9px #4e4e4e);
    width: calc(300px * 0.4);
    height: calc(300px * 0.5);
  }
    .product-detail {
    border: 1px solid black;
    height: 25px;
    align-items: center;
    font-size: 0.9rem;
  }
}
